// Outline
.App {
    display: flex;
    justify-content: center;
    align-items: center;
        
    padding: 30px;
}

.title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    vertical-align: middle;
    margin-right: 20px;

    .text {
        color: darken(#66ccff, 15%);
    }
    
    .logo {
        width: 100%;
        max-width: 140px;
        aspect-ratio: 1/1;
    }
}

.mainBox {
    text-align: center;
    width: 100%;
    max-width: $break-width;
    
    padding-top: 20px;
    pading-left: 10px;
    padding-right: 10px;
    padding-bottom: 15px;
    
    border-radius: 10px;
    background-color: white;
    box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.2);
}

.topBox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

/* Responsive */
@media (max-width: $break-width) { 
    // Mobile screen
    .App {
        width: 100%;
        height: 100%;
        min-width: 700px;
    }
    
    .mainBox {
        max-width: none;
        height: 100%;
        width: 100%;
        box-shadow: none;
        background-color: transparent;
    }
    
    // styling          
    .inputForm {
        .inputList {
            background-color: white;
            padding: 10px 20px 5px 20px;
            margin-bottom: 10px;
            border-radius: 10px;
            box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.6);
        }
    }
    
    .submitButtons {
        button {
            background-color: white;
            box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.6);
        }
    }
    
    .tableList {
        background-color: white;
        border-radius: 0px 0px 15px 15px;
        box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.6);
        
        tbody {
            border-botton: 1px solid gray;
        }
    }
}


// Form
.errorField {
    outline: 2px solid red !important;
}

.inputForm {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 400px;

    .inputList {
        display: flex;
        flex-direction: column;
        width: 100%;
        
        label {
            margin-bottom: 10px;
            display: flex;
            flex-direction: column;     
        }
        
        input {
            padding: 4px;
            border: 1px solid lighten(gray, 30%);
            border-radius: 4px;
            color: darken(gray, 30%);
        }
        span {
            vertical-align: middle;
            text-align: left;
            font-weight: bold;
        }
    }
}

// buttons
button {
    margin: 2px;
    height: 30px;
    padding-left: 10px;
    padding-right: 10px;
    border: none;
    border-radius: 5px;
    text-align: center;
}

button:hover {
    filter: brightness(85%);
}

button:active {
    filter: brightness(70%);
    outline: 3px solid #99ffff;
}


.submitButtons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    padding-left: 0px;
    
    button {
        min-width: 120px;
        margin: 15px;
    }
    
    // variants
    .delete {
        background-color: red;
        color: white;
    }
    .create {
        background-color: green;
        color: white;
    }
    .update {
        background-color: gold;
    }
    .clear {
        background-color: darken(white, 10%);
    }
}

// Table
.focusBtn { 
    background-color: blue;
    color: white;
}

.focusText {
    color: blue;
}

.tableList {
    width: 100%;
    margin-top: 11px;
    padding-top: 0px;
    
    border-collapse: collapse; 
    border: none;
    border-top: 1px solid gray;
        
    th {
        padding-top: 10px;
        font-size: 18px;
        color: black;
    }
    
    tr {
        color: gray;
        border-top: 1px dotted gray;
    }
}
